import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Box, IconButton, Input, Stack, Typography } from '@material-ui/core';
import { PhotoCamera } from '@material-ui/icons';

export default function UploadPhoto({ setValues }) {
  const [file, setFile] = useState(null);
  const [error, setError] = useState(null);
  const types = ['image/png', 'image/jpeg', 'image/jpg'];

  const changeHandler = (e) => {
    const selected = e.target.files[0]; // Seleccionar SOLO el primer archivo cargado en el input

    if (selected && types.includes(selected.type)) {
      setFile(selected);
      setValues((prevValues) => ({ ...prevValues, file: selected }));
      setError('');
    } else {
      setFile(null);
      setError('Por favor selecciona un archivo válido (.png, .jpg or .jpeg)');
    }
  };
  return (
    <>
      <Stack
        direction="row"
        justifyContent="center"
        alignItems="center"
      >

        <label htmlFor="icon-button-file">
          <Input
            sx={{ display: 'none' }}
            id="icon-button-file"
            type="file"
            onChange={changeHandler}
          />
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
          >
            <PhotoCamera />
          </IconButton>
        </label>
        <Typography
          color="text.secondary"
          sx={{ fontWeight: 100, fontSize: 15 }}
          variant="span"
        >
          {' '}
          Adjunte una foto de su pedido donde se evidencie el daño
          {' '}
        </Typography>
      </Stack>
      <Box>
        {error && (
        <Typography
          color="text.secondary"
          sx={{ fontWeight: 100, fontSize: 15 }}
        >
          {' '}
          {error}
          {' '}
        </Typography>
        )}
        {file && (
        <Typography
          color="text.secondary"
          sx={{ fontWeight: 100, fontSize: 15 }}
        >
          {' '}
          {file.name}
          {' '}
        </Typography>
        )}
      </Box>
    </>
  );
}

UploadPhoto.propTypes = {
  setValues: PropTypes.func
};
