import { useState } from 'react';
import PropTypes from 'prop-types';
import { Rating, TextField, Typography } from '@material-ui/core';

export default function UserInputs({ setValues }) {
  const [value, setValue] = useState(null);
  const [starComment, setStarComment] = useState(null);
  const [userComment, setUserComment] = useState('');

  function handleStars(e) {
    const starVal = Number(e.target.value);
    setValue(starVal);
    setValues((prevValues) => ({ ...prevValues, rate: starVal }));
    const comments = [
      'PESIMO 🤬',
      'MALO 😠',
      'PUEDE MEJORAR 🙂',
      'BUENO 😀',
      'EXCELENTE 😍',
    ];
    setStarComment(comments[starVal - 1]);
  }

  function handleComment(e) {
    const comment = e.target.value;
    setUserComment(comment);
    setValues((prevValues) => ({ ...prevValues, userComment: comment }));
  }

  return (
    <>
      <Rating
        sx={{ paddingTop: 2 }}
        name="simple-controlled"
        value={value}
        onChange={(event) => handleStars(event)}
        size="large"
      />

      <Typography
        sx={{
          paddingBottom: 4,
          paddingTop: 2,
          fontSize: 25,
          fontWeight: 'bold',
        }}
        variant="body2"
      >
        {starComment}
      </Typography>
      <TextField
        required
        id="outlined-required"
        label="¡Dejanos tu comentario!"
        multiline
        rows={8}
        size="medium"
        sx={{ width: 450 }}
        value={userComment}
        onChange={(event) => handleComment(event)}
      />
    </>
  );
}

UserInputs.propTypes = {
  setValues: PropTypes.func
};
