import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import {
  Typography,
  Card,
  CardContent,
  Button,
  Stack,
  FormControlLabel,
  Checkbox,
} from '@material-ui/core';
import UploadPhoto from './UploadPhoto';
import UserInputs from './UserInputs';

export default function CardRating() {
  const [checked, setChecked] = useState(false);
  const [values, setValues] = useState({});

  const query = new URLSearchParams(useLocation().search);
  const queryToken = query.get('token');

  useEffect(() => {
    setValues((prevValues) => ({ ...prevValues, token: queryToken }));
  }, [queryToken]);

  function handleChecked(e) {
    setChecked(e.target.checked);
    setValues((prevValues) => ({ ...prevValues, damageReport: e.target.checked }));
  }

  return (
    <Card
      sx={{
        textAlign: 'center',
        maxWidth: 600,
        padding: 5,
      }}
    >
      <CardContent>
        <Typography
          sx={{ fontWeight: 500, fontSize: 35 }}
          variant="h3"
          color="text.primary"
          gutterBottom
        >
          NOS GUSTARIA CONOCER TÚ OPINIÓN
        </Typography>

        <Typography
          variant="h5"
          component="div"
          color="text.secondary"
        >
          ¿Cómo calificas el servicio prestado?
        </Typography>
        <UserInputs setValues={setValues} />
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="center"
        >

          <FormControlLabel
            control={(
              <Checkbox
                checked={checked}
                onChange={(event) => handleChecked(event)}
              />
)}
            label="¿Desea reportar algun daño en su pedido?"
          />
          {
            checked && <UploadPhoto setValues={setValues} />
          }
        </Stack>
      </CardContent>
      <Button
        onClick={() => console.log(values)}
        variant="contained"
      >
        Calificar
      </Button>

    </Card>
  );
}
